html body {
  margin: 0;
  padding: 0;
  width: 100%;
  font-family: "Helvetica Neue", sans-serif;
}

.aboutContainer p{
  font-size: 2vw;
  margin-left: 0.5em;
}
a {
  text-decoration: none;
  margin-left: 1em;
  color: black;
}
a:hover{
  text-decoration: none;
  color: darkgrey;
}
input {
    width: 100%;
    max-width: 100%;
    padding: 6px;
    line-height: 30px;
    display: block;
    font-weight: 100;
    font-size: 2vw;
    position: relative;
    z-index: 2;
    -webkit-appearance: none;
    height: 100%;
    margin: 0 !important;
    border: 0;
    border-bottom: 1px solid #d8d8d8;
    outline: 0;
    text-align: center;
}

input[type="text"]{
    margin-top: 0;
    font-weight: 100;
    font-size: 2vw;
}
.main{
  width: 100%;
  margin: 1em;
}
.mainImg{
  width: 70%;
}
.cardImg {
  width: 40%;
  height: 17em;
  transition: transform .2s;
}

.cardImg:hover{
  position: absolute;
  width: 20%;
  height: 20em;
  transform: scale(1.2);
}

.navigation {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  justify-content: center;
  width: 100%;
  letter-spacing: 2px;
  margin: 0 !important;
  height: 3rem;
  line-height: 3rem;
  font-size: 1.5vw;
  vertical-align: middle;
}

button {
  display: none;
}

select{
  width: 100%;
  height: 3em;
  border: 0px;
  border-bottom: 1px solid black;
  text-align: center;
  text-align-last: center;
  font-size: 2vh;
}
*:focus {
  outline: 0;
}
.selectBox{
  text-align: center;
}

.list {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-auto-rows: auto;
  margin-top: 3em;
  margin: 2em 2em;
}
.title{
  font-size: 1.2em;
}

.card-container{
  display: flex;
  align-content: center;
  margin-top: 2em;
  margin-bottom: 4.5em;
}

.desc{
  margin-left: 1em;
  width: 50%;
}